import React from "react";
import about from "./About.module.css";
import data from "../Massiv/Massiv";
import { useParams } from "react-router-dom";
import { Card } from "antd";
import "antd/dist/antd.css";
import { useCart } from "react-use-cart";
const { Meta } = Card;
const About = () => {
  const params = useParams();
  const currentData = data.find((el) => {
    return el.id === params.id;
  });
  const { addItem } = useCart();
  return (
    <div>
      <div className="container">
        <h3>Home / Shop</h3>
        <div className="row">
          <div className="col-lg-4 col-md-6 col-sm-12">
            <Card
              hoverable
              style={{ width: 240 }}
              cover={<img src={currentData.image} alt="" />}
            >
              <Meta title={currentData.brand} />
              <p className={about.color}>{currentData.price}</p>
            </Card>
            <h5>Short Description:</h5>
            <p>
              The ceramic cylinder planters come with a wooden stand to help
              elevate your plants off <br /> the ground. The ceramic cylinder
              planters come with a wooden stand to help elevate <br /> your
              plants off the ground.{" "}
            </p>
            <h5>Size:</h5>
            <div className="d-flex">
              <h5 className={about.circle}>S</h5>
              <h5 className={about.circle}>M</h5>
              <h5 className={about.circle}>L</h5>
              <h5 className={about.circle}>XL</h5>
            </div>
            {/* <button
              className={about.circle2}
              onClick={() =>
                updateItemQuantity(currentData.id, currentData.count--)
              }
            >
              -
            </button>
            {currentData.count}
            <button
              className={about.circle2}
              onClick={() =>
                updateItemQuantity(currentData.id, currentData.count++)
              }
            >
              +
            </button> */}
            {/* <button className={about.butn}
            >Buy NOW</button> */}
            <button
              className={about.butn2}
              onClick={() => addItem(currentData)}
            >
              Add to cart
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;
