import React from 'react'
import { Card } from 'antd'
import 'antd/dist/antd.css'
import Sec from './Section.module.css'
import Sale from '../Section/gul.svg'
import Product from '../Product/Product'

const { Meta } = Card
const Section = () => {
  return (
    <div>
      <div className="container d-flex mt-5">
        <div className="section">
          <div className="categori ms-2">
            <h4 className="mt-3">Categories</h4>
            <div className="d-flex">
              <p>
                House Plants <br /> <br />
                Potter Plants <br /> <br />
                Seeds <br /> <br />
                Small Plants <br /> <br />
                Big Plants <br /> <br />
                Succulents <br /> <br />
                Trerrariums <br /> <br />
                Gardening <br /> <br />
                Accessories <br /> <br />
              </p>
              <p className={Sec.categor}>
                (33) <br /> <br />
                (12) <br /> <br />
                (65) <br /> <br />
                (39) <br /> <br />
                (23) <br /> <br />
                (17) <br /> <br />
                (19) <br /> <br />
                (13) <br /> <br />
                (18) <br /> <br />
              </p>
            </div>
            <h4>Price Range</h4>
            <input type="range" class="form-range" id="customRange1"></input>
            <p className="gred">Price: $39 - $1230</p>
            <button className={Sec.button}>Filter </button>
          </div>
          <br />
          <br />
          <h4>Size</h4>
          <div className="d-flex">
            <p>
              Small <br /> <br />
              Medium <br /> <br />
              Large <br /> <br />
            </p>
            <p className={Sec.categor}>
              (119) <br /> <br />
              (86) <br /> <br />
              (78) <br /> <br />
            </p>
          </div>
          <img src={Sale} alt="" className={Sec.widt} />
        </div>
        <Product/>
      </div>
    </div>
  )
}

export default Section
