import React from 'react'
import Gren from '../Header/gren.png'
import Hed from './Header.module.css'

const Header = () => {
  return (
    <div>
      <div className="container">
        <div className={Hed.or}>
          <div className="d-flex align-items-center">
            <div className={Hed.text}>
              <h4>Welcome to GreenShop</h4>
              <h1 className={Hed.gren}>
                Let’s Make a <br /> Better Planet
              </h1>
              <p>
                We are an online plant shop offering a wide range of cheap and
                trendy plants. Use <br /> our plants to create an unique Urban
                Jungle. Order your favorite plants!
              </p>{' '}
              <button className={Hed.button}>SHOP NOW</button>
            </div>
            <img src={Gren} alt="" />
          </div>
        </div>
      </div>
    </div>
  )
}

export default Header
