import React from "react";
 import { NavLink } from "react-router-dom";
import { Card } from "antd";
import "antd/dist/antd.css";
import Sec from "./Product.module.css";
import data from "../Massiv/Massiv";
import { useCart } from "react-use-cart";
const { Meta } = Card;
const Product = () => {
  // const {addItem, updateItemQuantity, removeItem} = useCart();
  return (
    <div>
      <div className="container">
        <div className="row">
          {data.map((el) => {
            return (
              <div key={el.id} className="col-lg-4 col-md-6 col-sm-12 mt-5 ">
                <NavLink to={`/${el.id}`}>
                  {" "}
                  <Card
                    hoverable
                    style={{ width: 240 }}
                    cover={<img src={el.image} alt="" />}
                  >
                    <Meta title={el.brand} />
                    <p className={Sec.color}>{el.price}</p>
                    {/* <button onClick={() => addItem(el)}>Add to cart</button>
                  <button onClick={() =>
                updateItemQuantity(el.id, el.count--)
              }>-</button>
                  {el.count}
                  <button onClick={() =>
                updateItemQuantity(el.id, el.count++)
              }>+</button>
              <button onClick={() => removeItem(el.id)}>Delete</button> */}
                  </Card>
                </NavLink>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default Product;
