import React from "react";
import { useCart } from "react-use-cart";

const Cart = () => {
  const { items, updateItemQuantity, removeItem,emptyCart,isEmpty } = useCart();
  if (isEmpty) return <h1>Cart is Empty!!!!!!</h1>
  let totalPrice = 0 

  return (
    <>
    <div className="flowers">
      {items.map((item) => {
        if (item.count >= 1) {
          const priceCounter = item.count * item.price;
          totalPrice += priceCounter
          return (
            <div className="flower text-center">
              <img className="w-100" src={item.image} alt="" />
              <h2>{item.brand}</h2>
              <h4>${priceCounter}</h4>
              <div className="counts d-flex justify-content-center">
                <button
                  onClick={() => updateItemQuantity(item.id, item.count--)}
                  className="btn btn-primary"
                >
                  -
                </button>
                <h6 className="mt-2">{item.count}</h6>
                <button
                  onClick={() => updateItemQuantity(item.id, item.count++)}
                  className="btn btn-primary"
                >
                  +
                </button>
                <button
                  onClick={() => removeItem(item.id)}
                  className="btn btn-danger"
                >
                  Remove
                </button>
               
              </div>
            </div>
          );
          
        } else {
          removeItem(item.id);
        }
      })}
    </div>
    <h1>${totalPrice}</h1>
    <button className="btn btn-primary" onClick={emptyCart}>Empty</button>
    </>
  );
};

export default Cart;
